.SimpleButton-bg, .SimpleButton-nobg {
	display: inline-block;
	height: 30px;
	width: 75px;
	cursor: pointer;
	-webkit-transition: all .2s ease;
	-o-transition: all .2s ease;
	transition: all .2s ease;
}
.SimpleButton-nobg {
	color:#009edd;
}
.SimpleButton-nobg:hover { 
	color: white;
}
.SimpleButton-text {
	line-height: 30px;
	text-align: center;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.SimpleButton-bg {
	background: #009edd; 
	border-radius: 5px;
	margin-right: 15px;	
}
.SimpleButton-bg:hover {
	-webkit-transform: scale(1.05);
	    -ms-transform: scale(1.05);
	        transform: scale(1.05);
}


