.MainPanel {
	background: -webkit-gradient(linear, left top, left bottom, from(#ededed), to(#cccccc));
	background: -webkit-linear-gradient(#ededed, #cccccc);
	background: -o-linear-gradient(#ededed, #cccccc);
	background: linear-gradient(#ededed, #cccccc);
	display: inline-block;
	height: 100%;
	position: relative;
	width: calc(100% - 350px);
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	overflow: hidden;
}
.Router-handle {
	cursor: all-scroll;

}
.MainPanel-coverageArea {
	background: rgba(68, 137, 244, 0.4);

	z-index: -1;
}
.MainPanel-coverageArea div {
	-webkit-animation: pulse 2.5s ease-in-out;
	        animation: pulse 2.5s ease-in-out;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	background: rgba(68, 137, 244, 0.2);
	border-radius: 50%;
	height: inherit;
	width: inherit;
}
.Router-image {
	height: inherit;
	position: absolute;
	width: inherit;
}
.Receiver {
	border-radius: 50%;
	height: 20px;
	position: absolute;
	width: 20px;
}
.Receivers-wrapper {
    position: absolute;
    top: 0;
}
@media screen and (max-width: 700px) {
    .MainPanel {
    	display: block;
    	height: 100%;
    	width: 100%;
    }
}
@-webkit-keyframes pulse {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
 }
 100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
 }
}
@keyframes pulse {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
 }
 100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
 }
}


